iframe
{
    position:relative;
    box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 10px rgba(0, 0, 0, 0.4) inset;
}
iframe:before, iframe:after
{
  content:"";
    position:absolute;
    z-index:-1;
    top:10px;
    bottom:10px;
    left:0;
    right:0;
    border-radius:100px / 10px;
}
iframe:after
{
  right:10px;
    left:auto;
    -webkit-transform:skew(8deg) rotate(3deg);
            transform:skew(8deg) rotate(3deg);
}

